.About {
    /* position: absolute; */
    padding: 0 2em 0 2em;
    color: white;
    height: 100vh;
    text-align: center;
    background-color: #103623;
    background: -webkit-linear-gradient(#103623 80%, #540934 100%);
    font-size: 5em;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-row: 1 / 2;
}

.About_top {
    padding: 2em 0 0em 0;
    grid-row: 1 / 2;
    grid-column: 1 / 2;
}
.About_bottom {
    padding: 0em 0 0em 0;
    grid-row: 2 / 3;
    grid-column: 1 / 2;
    font-size: 24px;
}